import React, { Component } from 'react';

class Email extends Component {
	render() {
		return (
			<div className="col l12 m12 s12 mobile sidebar-item">
				<div className="row margin-bottom-none">
					<div className="col m12 s12 l3 icon">
						<i className="fa fa-envelope" />
					</div>
					<div className="col m12 s12 l9 info">
						<div className="section-item-details">
							<div className="personal">
								<h4 className="no-margin">
									<a href="mailto:w.radduso@gmail.com">
										w.radduso@gmail.com
									</a>
								</h4>
								<span>email</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Email;



