import React, { Component } from "react";

class Info extends Component {
    render() {
        return (
            <div className="col l12 m12 s12 sort-info sidebar-item">
                <div className="row margin-bottom-none">
                    <div className="col m12 s12 l3 icon">
                        <i className="fa fa-user" />
                    </div>
                    <div className="col m12 s12 l9 info">
                        <div className="section-item-details">
                            <p>
                                I was born in San Justo, Santa Fe, Argentina on
                                February 10, 1986. A couple of years ago Ive
                                moved to Santa Fe City in Argentina. 
							</p>
							<p>
								When I was
                                younger I loved computers, many years ago I met
                                the language C and created my first application.
                                Then I met PHP and thats when I discovered my
                                real passion, being a web developer. Since then,
                                I kept myself continuously learning and
                                improving the way of writing better code,
                                everyday.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Info;



