import React, { Component } from 'react';

import Heading from './LeftSide/Heading';
import Info from './LeftSide/Info';
import Mobile from './LeftSide/Mobile';
import Email from './LeftSide/Email';
import LinkedIn from './LeftSide/LinkedIn';
import Skills from './LeftSide/Skills';

class LeftSide extends Component {
	render() {
		return (
			<aside className="col l4 m4 s12 sidebar z-depth-1" id="sidebar">
				<div className="row">
					<Heading />
					<Info />
					<Mobile />
					<Email />
					<LinkedIn />
					<Skills />
				</div>
			</aside>
		);
	}
}

export default LeftSide;



